        (包括但不限於狀態更新、貼文、訊息及/或聊天室)。您同意您了解且已評估使用任何內容的所有相關風險，包括但不限於對於這類內容之正確性、完整度或實用性的倚賴 6.5
        您承認、允許並同意，若依法律要求，或根據法院命令或對Shopee有管轄權之任何政府或監督主管機關之合法處分，或基於善意及誠信原則認有合理之必要性時，Shopee得存取、保存及/或揭露您的帳戶資訊和內容予任何法定或政府主管機關、相關權利人或其他第三方，以：(a)
        遵守法律程序；(b) 執行本服務條款或禁止和限制商品政策；(c) 回應任何侵害第三方權利（包括智慧財產權）的內容之申訴；(d) 回應您的客戶服務請求；或者 (e)
