        如前述，Shopee不允許刊登項目侵害屬於品牌或智慧財產權所有人（下稱「IPR所有人」）之智慧財產權。 8.2
        除另有明示排除外，使用者為獨立的個體或事業，與Shopee無任何關聯，且Shopee非使用者的代理人或代表人，亦不持有或擁有任何本網站上刊登的商品。 8.3
        如果您為IPR所有人或取得IPR所有人合法授權的代理人（下稱「IPR代理人」），且您認為您或您的委託人的作品 (在本服務中)
        遭到複製、展示或散布之情形已構成侵害智慧財產權已遭受侵害之行為，請以電子郵件通知我們：help@support.shopee.tw，另請提供下列所需資料以支持您的主張。請給予我們時間處理資料，Shopee將於可行範圍內盡速回覆您的通知。
        8.4 依本條所為的侵權通知必須以Shopee指定的格式（Shopee得隨時更新）提供，並確實附上至少下列所有資料：(a)IPR所有人或IPR代理人代理人（下合稱「通知者」）的實體或電子簽名；(b)
        主張遭到侵害之智慧財產權的種類和本質的描述，以及該權利的證明；(c) 對主張遭到侵害之智慧財產權本質的描述需附上充分細節足以讓Shopee評估侵權通知; (d) 包含所指侵權內容的具體刊登商品URL網址連結；(e)
        可讓Shopee聯絡到通知者的資訊，例如通知者的實際地址、電話號碼和電子郵件地址；(f) 通知者基於誠信提出侵權通知，且所通知之智慧財產權使用方式經IPR所有人之授權或合法之聲明；(g)
        通知者聲明此通知所載之資訊均真實無誤，如有不實，通知者願賠償Shopee因通知者所提供資料造成的任何損失，且通知者具有合法適當權限或已被授權代表IPR所有人處理與此侵權通知一切相關事宜。 8.5
        Shopee了解廠商為其產品簽訂獨家經銷協議或最低廣告價格協議的權利，然而違反此等協議不構成智慧財產權之侵害。此等協議的強制執行屬於廠商與銷售者之間事務，Shopee不協助這方面的強制執行活動且不執行獨家經銷權或價格控制事宜，除非所在國法律明確規範選擇性配銷或獨家經銷。
        8.6 賣家同意擔保Shopee及其關係企業免於承擔因依據或與侵害智慧財產權主張相關移除內容或刊登商品而導致任何主張、請求、求償及裁判所造成的損害。 9. 購買與付款 9.1
        Shopee或其合作之金流服務商在其運營的國家支援一種或多種下列付款方式： (i) 信用卡 信用卡付款是透過第三方支付管道處理款項，而這些支付管道所接受的信用卡類型會因您所在的管轄地不同而有差異。 (ii)
        貨到付款（COD） Shopee或其合作之金流服務商在其選定的國家提供COD服務，買家得於收到購買的商品時直接給付現金給運送人。 (iii) 銀行轉帳 買家可透過自動提款機或網路銀行轉帳 (下稱「銀行轉帳」)
        將款項匯入我方指定的Shopee履約保證帳戶 (如第 11
        條所定義)。買家必須透過Shopee或其合作之金流服務商應用程式中的「上傳收據」功能，向Shopee或其合作之金流服務商提供轉帳單據或付款交易參考，以供查驗。如果Shopee或其合作之金流服務商未在三天內收到付款確認，將會取消買家訂單。
        9.2 買家只能在付款前變更付款方式。 9.3
        買家支付完成前，應依Shopee提供的再確認機制確認支付指示是否正確。因不可歸責於消費者的事由而發生支付錯誤時，Shopee或其合作之金流服務商將協助消費者更正並提供其他必要之協助。因可歸責於Shopee或其合作之金流服務商的事由而發生支付錯誤時，Shopee或其合作之金流服務商將於知悉時立即更正，並同時以電子郵件、簡訊或App推播等方式通知消費者。因可歸責於消費者的事由而發生支付錯誤時，例如輸入錯誤之金額或輸入錯誤之收款方，經消費者通知後，Shopee或其合作之金流服務商將立即協助處理。我們有權查核買家是否具備使用特定付款方式之充分授權，並可在授權獲得確認之前暫停交易，或在未能獲得確認的情況下取消相關交易。買家完成每筆款項支付後，Shopee將以電子郵件、簡訊或App推播等方式通知買家支付明細，並於買家的蝦皮錢包即時顯示支付明細供買家查詢。
        9.4
        如Shopee或其合作之金流服務商依本服務條款或相關政策需透過銀行轉帳方式支付您商品銷售金額或退款，您應提供本人所有且與Shopee帳戶姓名（或名稱）相符之正確銀行帳戶資料給Shopee或其合作之金流服務商；為確保交易安全，Shopee並得就您所提供之資料進行認證；若您尚未設定有效之銀行帳戶並留存相關資料、或所提供之銀行帳戶姓名（或名稱）及/或相關資料與您Shopee帳戶所留存的資料不一致、或無法就您所提供之資料完成認證，Shopee得拒絕撥付款項給您，並暫時保留款項直到您提出與所留存資料一致之銀行帳戶資料予Shopee、或完成資料認證時止，必要時，Shopee並得拒絕或暫停提供您本服務之全部或一部。
        10. 蝦皮錢包 10.1
        蝦皮錢包係由Shopee或其合作之金流服務商提供的服務，便利使用者管理其於本平台的銷售商品款項及透過銀行轉帳、貨到付款或蝦皮錢包等方式所進行的購物相關退貨款項。以上款項總金額扣除已提領的金額後即為蝦皮錢包顯示的餘額。
        10.2
        您可以在本平台提交提領需求(下稱「提領需求」)將您蝦皮錢包的餘額匯入您所綁定的銀行帳戶(下稱「綁定帳戶」)，每週得免費提領一次(每週一重新計算次數)，超過每次將收取提領手續費10元(從提領的款項內直接扣除)。Shopee或其合作之金流服務商得視情形決定提供將蝦皮錢包餘額定期自動提領至您綁定帳戶的功能。Shopee或其合作之金流服務商僅在工作日處理提領需求，每一次提領需3至5個工作日的作業時間。
        10.3 買家購物金額將於商品送達買家或買家提交收到商品的確認後一天內計入賣家的蝦皮錢包餘額。退貨退款金額將於退貨退款請求被核准後一天內計入買家的蝦皮錢包餘額。 10.4
        提領需求經提交後不得修改或撤回。 10.5
        若交易處理過程中發生任何錯誤，您授權我們在適用法規允許範圍內調整您綁定帳戶的支出或存入紀錄以修正錯誤，若因任何原因導致我們無法從您的綁定帳戶扣款，您授權我們在適用法規允許範圍內從您與蝦皮或其合作之金流服務商曾往來的其他銀行帳戶或支付工具中扣款(含相關費用)或從您的蝦皮錢包餘額中扣抵。
        10.6 您授權我們於下列情形調整您蝦皮錢包的支出或存入記錄： (i) 更正任何交易處理過程中發生的錯誤； (ii) Shopee認定您涉及詐騙或可疑活動及/或交易； (iii) 與任何遺失、損壞或錯誤的商品有關； (iv)
        與任何回饋或補貼有關； (v) 與任何尚未收取之費用有關； (vi) 與任何交易糾紛有關，包括任何因您引起或由您所給付的賠償或補償； (vii) 與任何被禁止的商品或被海關扣留之商品有關；及 (viii)
        與任何買賣雙方合意之變更有關。 11. Shopee履約保證 11.1
        Shopee履約保證是Shopee或其或其合作之金流服務商提供的一項購物保障服務。為提供責任風險之保障，藉由使用本服務而支付給賣家的購物款項將由Shopee或其或其合作之金流服務商保管
        (下稱「Shopee履約保證帳戶」) 中。對於您支付到Shopee履約保證帳戶中的款項，賣家不會收取利息或其他收益。 11.2 買家支付其訂單款項 (下稱「買家購物金額」)
        後，買家購物金額將保留於Shopee履約保證帳戶中，直到： (a) 買家提供已收到其訂購商品之確認予Shopee，於此種情形，除非有第11.2
        (d)條之適用，Shopee或其合作之金流服務商會將保管於Shopee履約保證帳戶中的買家購物金額支付給賣家； (b) Shopee履約保證期間(或於依第 11.3 條展延之期間) 屆滿，於此種情形，除非有第11.2 (c)條或第11.2
        (d)條之適用，Shopee或其合作之金流服務商會將保管於Shopee履約保證帳戶中的買家購物金額支付給賣家； (c) Shopee認定買家退還商品及/或退款之申請應通過時，於此種情形，除非有第11.2
        (d)條之適用，Shopee或其合作之金流服務商將根據退款與退貨政策提供退款予買家； (d)
        在其他情形Shopee或其合作之金流服務商合理認定對於買家購物金額適當之處置，包含但不限於其為符合適用之法律、法院命令或為實行本服務條款而合理認為有必要之處置。
        Shopee履約保證僅適用於透過Shopee或其合作之金流服務商提供之管道付款至Shopee履約保證帳戶的買家。買家與賣家之間的離線交易，不在Shopee履約保證之範圍內。 11.3
        透過Shopee或其合作之金流服務商提供之管道支付的款項將會保管於Shopee履約保證帳戶中一段特定時間
        (下稱「Shopee履約保證期間」)。如需Shopee履約保證期間的詳細資訊，請按此連結。買家可在適用之Shopee履約保證期間屆滿前，申請延長Shopee履約保證期間一次（受限於且應根據退款與退貨政策）。買家完成申請後，除非Shopee根據其單方考量決定更長的延展期間較為適當或必要外，Shopee履約保證期間可最多延長三天。
        11.4
        出於任何原因導致無法將款項匯入賣家的銀行帳戶及/或無法聯繫上賣家，Shopee將盡合理努力嘗試以賣家所提供之聯絡資訊與賣家取得聯繫。當買家購物金額自應支付給賣家時起算超過十五年仍無人認領且無法聯繫上賣家時，Shopee將依相關適用之法律處置此買家購物金額。
        11.5
        賣家/買家必須是帳戶的權益擁有人，並且只能代表他或她本人來進行本網站上交易。使用者應依Shopee建立的身分認證機制開設帳戶，包括但不限於提供電話號碼、地址、銀行帳戶、身分證字號、最新身分證件照片等個人資料及其他必要文件，以供查驗之用。賣家/買家茲同意Shopee在或其合作之金流服務商便利賣家/買家使用本網站之目的範圍內處理或提供其個人資料給第三人並授權Shopee使用其個人資料向適當主體
        (如賣家/買家的銀行) 進行我方認為必要之賣家身分驗證程序。有關Shopee如何處理您個人資料的詳細資訊，請瀏覽我們的隱私權政策頁面。 11.6
        Shopee履約保證是附加且未限制買家及賣家在所適用法律下的義務，該法律義務可能高於Shopee履約保證。Shopee履約保證既無意圖亦未被設計用於協助買家或賣家遵守其法律義務，其仍就該法律義務自行負責，且Shopee不承擔與該法律義務有關之責任。除此之外，Shopee履約保證不構成產品擔保。
        11.7 買家及賣家知悉且同意Shopee有關Shopee履約保證之決定（包含任何要求）是最終的。 11.8 為避免疑義，任何非在本網站進行的交易，不在Shopee履約保證之範圍內。 12. 蝦幣回饋機制 12.1
        使用者可以藉由其在本網站適用Shopee履約保證的購物行為或者參與其他Shopee不定期決定之活動(下稱「適用活動」)依Shopee所規定的兌換比例獲得紅利點數(下稱「蝦幣」)。除非Shopee另有規定，蝦幣將在Shopee確認使用者已完成適用活動後撥入使用者帳戶。使用者均得參與蝦幣回饋機制除非您的帳戶內已明示排除適用。
        12.2 不在Shopee履約保證範圍內的交易行為或活動不適用蝦幣回饋機制。Shopee得單獨決定特定類型項目排除在蝦幣回饋機制外。 12.3
        給予買家的蝦幣不具有貨幣價值，亦不構成買家之資產，且不得出售、轉讓予其他使用者或任何第三人。買家不得要求將蝦幣折算成現金。 12.4
        使用者可以將蝦幣依Shopee所規定當時適用之兌換標準、辦法及限額（Shopee保留隨時變更、修改、暫停或終止該標準、辦法及限額之權利）請求折抵本網站消費金額。關於適用活動下發生的退款應依第14.4條Shopee退款與退貨政策之規定。
        12.5 已折抵的蝦幣將從您帳戶裡的蝦幣累積餘額中扣除。蝦幣均訂有使用期限。請留意本網站您帳戶內所列蝦幣累積餘額及使用期限。 12.6
        我們將不定期推出適用活動，包括但不限於購買有參與適用活動賣家的商品或其他特定行銷活動。每次適用活動的相關辦法我們會即時通知您。 12.7
        如果您對參加適用活動所獲得的蝦幣數額有疑義，應於適用活動起一個月內向Shopee提出，並依Shopee要求提出足以支持您的主張之證據資料。 12.8
        Shopee不保證亦不承擔蝦幣可能產生的課稅或稅務處理責任，您應尋求您的稅務顧問關於蝦幣影響您稅務情形的意見。 12.9
        Shopee保留以下權利：(i)隨時終止蝦幣回饋機制，及(ii)取消、暫停或限制任何使用者適用蝦幣回饋機制之權利，包括但不限於累積或折抵蝦幣。 12.10
        如果您使用蝦幣折抵本平台消費金額後，該訂單發生取消訂單或退貨退款情事，系統將回復已折抵的蝦幣數額至您的Shopee帳戶；如果您因於本平台蝦皮商城消費而獲得蝦幣，但該訂單發生取消訂單或退貨退款情事，則系統將自動扣回您Shopee帳戶裡同等數額蝦幣，若不足扣回，蝦幣數額將顯示負數，並於您將來消費所獲得之蝦幣中優先扣回。
        13. 蝦幣行銷服務 13.1
